.code-editor {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--bg-primary);
}

.code-editor.no-files {
  display: flex;
  align-items: center;
  justify-content: center;
}

.editor-tabs {
  display: flex;
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  overflow-x: auto;
  flex-shrink: 0;
}

.editor-tabs::-webkit-scrollbar {
  height: 3px;
}

.editor-tabs::-webkit-scrollbar-track {
  background: transparent;
}

.editor-tabs::-webkit-scrollbar-thumb {
  background: var(--bg-tertiary);
  border-radius: 2px;
}

.editor-tab {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: var(--bg-tertiary);
  border-right: 1px solid var(--border-color);
  cursor: pointer;
  font-size: 13px;
  color: var(--text-secondary);
  transition: all 0.2s ease;
  min-width: 0;
  max-width: 200px;
  position: relative;
  white-space: nowrap;
}

.editor-tab:hover {
  background: var(--bg-secondary);
  color: var(--text-primary);
}

.editor-tab.active {
  background: var(--bg-primary);
  color: var(--text-primary);
  border-bottom: 2px solid var(--accent-purple);
}

.tab-icon {
  font-size: 14px;
  flex-shrink: 0;
}

.tab-name {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.unsaved-indicator {
  color: var(--accent-orange);
  font-size: 12px;
  flex-shrink: 0;
}

.tab-close {
  background: transparent;
  border: none;
  color: var(--text-muted);
  font-size: 12px;
  padding: 2px 4px;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.2s ease;
  opacity: 0;
  flex-shrink: 0;
}

.editor-tab:hover .tab-close {
  opacity: 1;
}

.tab-close:hover {
  background: var(--accent-red);
  color: white;
}

.editor-content {
  flex: 1;
  background: var(--bg-primary);
  min-height: 0;
}

.welcome-screen {
  text-align: center;
  padding: 40px;
  max-width: 600px;
  margin: 0 auto;
}

.welcome-screen h2 {
  color: var(--accent-purple);
  margin-bottom: 16px;
  font-size: 28px;
}

.welcome-screen p {
  color: var(--text-secondary);
  margin-bottom: 32px;
  font-size: 16px;
}

.quick-actions {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-bottom: 40px;
  flex-wrap: wrap;
}

.quick-action {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.quick-action:hover {
  background: var(--accent-blue);
  color: white;
  border-color: var(--accent-blue);
  transform: translateY(-2px);
}

.react-bits-info {
  background: var(--bg-secondary);
  padding: 24px;
  border-radius: 12px;
  border: 1px solid var(--border-color);
  text-align: left;
}

.react-bits-info h3 {
  color: var(--accent-purple);
  margin-bottom: 12px;
  font-size: 18px;
}

.react-bits-info p {
  color: var(--text-secondary);
  margin-bottom: 16px;
  font-size: 14px;
}

.react-bits-info ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.react-bits-info li {
  padding: 6px 0;
  color: var(--text-primary);
  font-size: 14px;
}

.react-bits-info code {
  background: var(--bg-tertiary);
  color: var(--accent-purple);
  padding: 4px 8px;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 13px;
}

/* Monaco Editor overrides */
.monaco-editor {
  background: var(--bg-primary) !important;
}

.monaco-editor .margin {
  background: var(--bg-primary) !important;
}

.monaco-editor .monaco-editor-background {
  background: var(--bg-primary) !important;
}

/* Responsive */
@media (max-width: 768px) {
  .editor-tabs {
    overflow-x: scroll;
  }
  
  .editor-tab {
    min-width: 120px;
    max-width: 150px;
  }
  
  .welcome-screen {
    padding: 20px;
  }
  
  .quick-actions {
    flex-direction: column;
    align-items: center;
  }
}