.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: var(--bg-primary);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.app.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
}

.main-content {
  flex: 1;
  min-height: 0;
}

/* Sleep Mode Page Styling */
.sleep-mode-page {
  padding: 20px;
  background: var(--bg-primary);
  color: var(--text-primary);
  min-height: calc(100vh - 60px);
  overflow-y: auto;
}

.sleep-mode-page .sleep-mode-controls {
  margin-bottom: 20px;
}

.sleep-mode-page .sleep-mode-manager {
  margin-top: 0;
}

.editor-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Split Pane Styling */
.Pane {
  background: var(--bg-primary);
}

.Pane:first-child {
  border-right: 1px solid var(--border-color);
}

.Pane:last-child {
  border-left: 1px solid var(--border-color);
}

/* Loading Screen */
.loading-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 1.2rem;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--bg-tertiary);
  border-top: 4px solid var(--accent-purple);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
  }
}

/* Panel Animations */
.panel-enter {
  opacity: 0;
  transform: translateX(-100%);
}

.panel-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms ease, transform 300ms ease;
}

.panel-exit {
  opacity: 1;
  transform: translateX(0);
}

.panel-exit-active {
  opacity: 0;
  transform: translateX(-100%);
  transition: opacity 300ms ease, transform 300ms ease;
}