/* Infinite Loop Page - Tokyo Night Theme */
.infinite-loop-page {
  height: calc(100vh - 60px);
  overflow-y: auto;
  background: linear-gradient(135deg, #1a1b26 0%, #24283b 50%, #1a1b26 100%);
  color: #a9b1d6;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  padding: 0;
}

.page-header {
  background: rgba(36, 40, 59, 0.8);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(116, 199, 236, 0.1);
  padding: 24px 32px;
  position: sticky;
  top: 0;
  z-index: 100;
}

.back-to-ide-btn {
  background: rgba(116, 199, 236, 0.1);
  border: 1px solid rgba(116, 199, 236, 0.3);
  color: #73daca;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.3s ease;
  margin-bottom: 16px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.back-to-ide-btn:hover {
  background: rgba(116, 199, 236, 0.2);
  border-color: rgba(116, 199, 236, 0.5);
  transform: translateX(-2px);
}

.page-header h1 {
  margin: 0 0 8px 0;
  font-size: 32px;
  font-weight: 700;
  background: linear-gradient(135deg, #73daca, #bb9af7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.page-header p {
  margin: 0;
  font-size: 16px;
  color: #9aa5ce;
  opacity: 0.9;
}

.infinite-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  padding: 32px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Configuration Panel */
.config-panel {
  background: rgba(36, 40, 59, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(116, 199, 236, 0.15);
  border-radius: 16px;
  padding: 24px;
  height: fit-content;
}

.config-panel h2 {
  margin: 0 0 24px 0;
  font-size: 20px;
  font-weight: 600;
  color: #bb9af7;
}

.config-section {
  margin-bottom: 32px;
}

.config-section h3 {
  margin: 0 0 16px 0;
  font-size: 16px;
  font-weight: 600;
  color: #73daca;
}

/* Spec Selector */
.spec-selector {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.spec-option {
  background: rgba(26, 27, 38, 0.6);
  border: 1px solid rgba(116, 199, 236, 0.2);
  border-radius: 12px;
  padding: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.spec-option:hover {
  background: rgba(26, 27, 38, 0.8);
  border-color: rgba(116, 199, 236, 0.4);
  transform: translateY(-1px);
}

.spec-option.selected {
  background: rgba(116, 199, 236, 0.1);
  border-color: #73daca;
  box-shadow: 0 0 0 2px rgba(115, 218, 202, 0.2);
}

.spec-name {
  font-weight: 600;
  color: #bb9af7;
  margin-bottom: 4px;
}

.spec-description {
  font-size: 14px;
  color: #9aa5ce;
  opacity: 0.8;
}

/* Custom Spec */
.custom-spec {
  margin-top: 16px;
}

.custom-spec label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #73daca;
}

.custom-spec textarea {
  width: 100%;
  background: rgba(26, 27, 38, 0.8);
  border: 1px solid rgba(116, 199, 236, 0.3);
  border-radius: 8px;
  padding: 12px;
  color: #a9b1d6;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 14px;
  resize: vertical;
  transition: border-color 0.3s ease;
}

.custom-spec textarea:focus {
  outline: none;
  border-color: #73daca;
  box-shadow: 0 0 0 2px rgba(115, 218, 202, 0.2);
}

/* Config Rows */
.config-row {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 16px;
}

.config-row label {
  font-weight: 500;
  color: #73daca;
}

.config-row input,
.config-row select {
  background: rgba(26, 27, 38, 0.8);
  border: 1px solid rgba(116, 199, 236, 0.3);
  border-radius: 8px;
  padding: 12px;
  color: #a9b1d6;
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.config-row input:focus,
.config-row select:focus {
  outline: none;
  border-color: #73daca;
  box-shadow: 0 0 0 2px rgba(115, 218, 202, 0.2);
}

/* Control Buttons */
.control-buttons {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

.start-btn,
.stop-btn,
.clear-btn {
  flex: 1;
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.start-btn {
  background: linear-gradient(135deg, #73daca, #9ece6a);
  color: #1a1b26;
}

.start-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(115, 218, 202, 0.3);
}

.stop-btn {
  background: linear-gradient(135deg, #f7768e, #ff9e64);
  color: #1a1b26;
}

.stop-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(247, 118, 142, 0.3);
}

.clear-btn {
  background: rgba(116, 199, 236, 0.1);
  border: 1px solid rgba(116, 199, 236, 0.3);
  color: #74c7ec;
}

.clear-btn:hover {
  background: rgba(116, 199, 236, 0.2);
  border-color: rgba(116, 199, 236, 0.5);
  transform: translateY(-2px);
}

/* Status Panel */
.status-panel {
  background: rgba(36, 40, 59, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(116, 199, 236, 0.15);
  border-radius: 16px;
  padding: 24px;
  height: fit-content;
}

.status-panel h2 {
  margin: 0 0 24px 0;
  font-size: 20px;
  font-weight: 600;
  color: #bb9af7;
}

/* Status Display */
.status-display {
  background: rgba(26, 27, 38, 0.6);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 24px;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.status-item:last-child {
  margin-bottom: 0;
}

.status-label {
  font-weight: 500;
  color: #9aa5ce;
}

.status-value {
  font-weight: 600;
  color: #73daca;
}

.status-value.running {
  color: #9ece6a;
}

.status-value.stopped {
  color: #f7768e;
}

.session-id {
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 12px;
  background: rgba(116, 199, 236, 0.1);
  padding: 4px 8px;
  border-radius: 4px;
}

/* Logs */
.logs-container {
  background: rgba(26, 27, 38, 0.6);
  border-radius: 12px;
  overflow: hidden;
}

.logs-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background: rgba(36, 40, 59, 0.8);
  border-bottom: 1px solid rgba(116, 199, 236, 0.1);
}

.logs-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: #bb9af7;
}

.log-count {
  font-size: 12px;
  color: #9aa5ce;
  background: rgba(116, 199, 236, 0.1);
  padding: 4px 8px;
  border-radius: 12px;
}

.logs-display {
  max-height: 400px;
  overflow-y: auto;
  padding: 16px;
}

.no-logs {
  text-align: center;
  color: #565f89;
  font-style: italic;
  padding: 40px 20px;
}

.log-entry {
  display: flex;
  gap: 12px;
  padding: 8px 0;
  border-bottom: 1px solid rgba(116, 199, 236, 0.05);
  font-size: 14px;
}

.log-entry:last-child {
  border-bottom: none;
}

.log-timestamp {
  color: #565f89;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 12px;
  min-width: 80px;
}

.log-message {
  flex: 1;
  color: #a9b1d6;
}

.log-entry.success .log-message {
  color: #9ece6a;
}

.log-entry.warning .log-message {
  color: #ff9e64;
}

.log-entry.error .log-message {
  color: #f7768e;
}

/* Info Footer */
.info-footer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  padding: 32px;
  max-width: 1400px;
  margin: 0 auto;
}

.info-card {
  background: rgba(36, 40, 59, 0.6);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(116, 199, 236, 0.15);
  border-radius: 16px;
  padding: 24px;
}

.info-card h3 {
  margin: 0 0 16px 0;
  font-size: 18px;
  font-weight: 600;
  color: #bb9af7;
}

.info-card ul {
  margin: 0;
  padding-left: 20px;
  color: #9aa5ce;
}

.info-card li {
  margin-bottom: 8px;
  line-height: 1.5;
}

.command-preview {
  display: block;
  background: rgba(26, 27, 38, 0.8);
  border: 1px solid rgba(116, 199, 236, 0.3);
  border-radius: 8px;
  padding: 12px;
  font-family: 'Monaco', 'Menlo', monospace;
  font-size: 14px;
  color: #73daca;
  margin: 12px 0;
  word-break: break-all;
}

.info-card p {
  margin: 12px 0 0 0;
  color: #9aa5ce;
  font-size: 14px;
}

/* Responsive */
@media (max-width: 1200px) {
  .infinite-content {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .info-footer {
    grid-template-columns: 1fr;
    gap: 24px;
  }
}

@media (max-width: 768px) {
  .page-header {
    padding: 20px;
  }
  
  .infinite-content {
    padding: 20px;
  }
  
  .info-footer {
    padding: 20px;
  }
  
  .control-buttons {
    flex-direction: column;
  }
}