.settings-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.settings-modal {
  background: #1e1e1e;
  border: 1px solid #404040;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #404040;
  background: #252525;
  border-radius: 12px 12px 0 0;
}

.settings-header h2 {
  margin: 0;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
}

.close-btn {
  background: none;
  border: none;
  color: #999;
  font-size: 24px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
  transition: all 0.2s;
}

.close-btn:hover {
  color: #ffffff;
  background: #404040;
}

.settings-content {
  padding: 24px;
}

.settings-section {
  margin-bottom: 32px;
}

.settings-section h3 {
  color: #ffffff;
  font-size: 18px;
  margin-bottom: 12px;
  font-weight: 600;
}

.settings-description {
  color: #cccccc;
  margin-bottom: 20px;
  line-height: 1.5;
}

.api-key-input-group {
  margin-bottom: 20px;
}

.api-key-input-group label {
  display: block;
  color: #ffffff;
  margin-bottom: 8px;
  font-weight: 500;
}

.input-with-toggle {
  position: relative;
  display: flex;
  align-items: center;
}

.api-key-input {
  flex: 1;
  background: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 6px;
  padding: 12px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Consolas', 'Monaco', monospace;
  transition: border-color 0.2s;
}

.api-key-input:focus {
  outline: none;
  border-color: #007acc;
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.2);
}

.toggle-visibility-btn {
  background: #404040;
  border: none;
  padding: 8px 12px;
  margin-left: 8px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.2s;
}

.toggle-visibility-btn:hover {
  background: #505050;
}

.settings-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.btn {
  padding: 10px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 6px;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: #007acc;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: #005a9e;
}

.btn-secondary {
  background: #404040;
  color: white;
}

.btn-secondary:hover:not(:disabled) {
  background: #505050;
}

.btn-danger {
  background: #d73a49;
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background: #b31d2c;
}

.test-result {
  padding: 12px;
  border-radius: 6px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

.test-result.success {
  background: #0f5132;
  border: 1px solid #198754;
  color: #d1e7dd;
}

.test-result.error {
  background: #58151c;
  border: 1px solid #dc3545;
  color: #f8d7da;
}

.result-icon {
  font-size: 16px;
}

.result-message {
  flex: 1;
}

.api-key-info {
  background: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 8px;
  padding: 16px;
  margin-top: 20px;
}

.api-key-info h4 {
  color: #ffffff;
  margin-bottom: 12px;
  font-size: 16px;
}

.api-key-info ol {
  color: #cccccc;
  margin: 0 0 16px 0;
  padding-left: 20px;
}

.api-key-info li {
  margin-bottom: 4px;
  line-height: 1.4;
}

.api-key-info a {
  color: #007acc;
  text-decoration: none;
}

.api-key-info a:hover {
  text-decoration: underline;
}

.api-key-status {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid #404040;
}

.status-badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
}

.status-badge.connected {
  background: #0f5132;
  color: #d1e7dd;
}

.status-badge.disconnected {
  background: #58151c;
  color: #f8d7da;
}

.feature-info {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.feature-item {
  background: #2d2d2d;
  border: 1px solid #404040;
  border-radius: 6px;
  padding: 12px;
}

.feature-item strong {
  color: #ffffff;
  display: block;
  margin-bottom: 4px;
}

.feature-item p {
  color: #cccccc;
  margin: 0;
  font-size: 14px;
  line-height: 1.4;
}

/* Responsive design */
@media (max-width: 768px) {
  .settings-modal {
    width: 95%;
    margin: 20px;
  }
  
  .settings-actions {
    flex-direction: column;
  }
  
  .btn {
    width: 100%;
    justify-content: center;
  }
  
  .input-with-toggle {
    flex-direction: column;
    gap: 8px;
  }
  
  .toggle-visibility-btn {
    margin-left: 0;
    align-self: flex-end;
  }
}