.parallel-agent-page {
  height: 100vh;
  overflow-y: auto;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f172a 100%);
  color: #e2e8f0;
  padding: 20px;
  padding-bottom: 40px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  box-sizing: border-box;
  scroll-behavior: smooth;
  /* Custom scrollbar styling for main container */
  scrollbar-width: thin;
  scrollbar-color: rgba(99, 102, 241, 0.6) rgba(30, 41, 59, 0.4);
}

.parallel-agent-page::-webkit-scrollbar {
  width: 12px;
}

.parallel-agent-page::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.4);
  border-radius: 6px;
}

.parallel-agent-page::-webkit-scrollbar-thumb {
  background: rgba(99, 102, 241, 0.6);
  border-radius: 6px;
  border: 2px solid rgba(30, 41, 59, 0.4);
}

.parallel-agent-page::-webkit-scrollbar-thumb:hover {
  background: rgba(99, 102, 241, 0.8);
}

.page-header {
  text-align: center;
  margin-bottom: 30px;
  position: relative;
}

.back-to-ide-btn {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(99, 102, 241, 0.1);
  border: 2px solid #6366f1;
  color: #a5b4fc;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 14px;
}

.back-to-ide-btn:hover {
  background: rgba(99, 102, 241, 0.2);
  color: #c7d2fe;
  border-color: #f97316;
  transform: translateY(-50%) translateX(-2px);
  box-shadow: 
    0 0 15px rgba(249, 115, 22, 0.5),
    0 0 30px rgba(249, 115, 22, 0.2);
}

.page-header h1 {
  font-size: 2.5rem;
  margin: 0;
  background: linear-gradient(135deg, #a78bfa, #06b6d4, #10b981);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px rgba(167, 139, 250, 0.3);
}

.page-header p {
  color: #94a3b8;
  margin: 10px 0 0 0;
  font-size: 1.1rem;
}

.parallel-content {
  max-width: 1400px;
  margin: 0 auto;
  min-height: 0; /* Allow flexbox shrinking */
  flex: 1;
  display: flex;
  flex-direction: column;
}

/* Setup Panel */
.setup-panel {
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 16px;
  padding: 30px;
  backdrop-filter: blur(10px);
}

.setup-panel h2 {
  color: #f1f5f9;
  margin: 0 0 25px 0;
  font-size: 1.5rem;
}

.config-section {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
}

.config-section h3 {
  color: #cbd5e1;
  margin: 0 0 15px 0;
  font-size: 1.1rem;
}

.config-row {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  gap: 15px;
}

.config-row label {
  min-width: 120px;
  color: #94a3b8;
  font-weight: 500;
}

.config-row input,
.config-row select {
  flex: 1;
  background: rgba(15, 23, 42, 0.8);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #e2e8f0;
  padding: 10px 12px;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.3s ease;
}

.config-row input:focus,
.config-row select:focus,
.config-row input:hover,
.config-row select:hover {
  outline: none;
  border-color: #f97316;
  box-shadow: 
    0 0 0 3px rgba(99, 102, 241, 0.1),
    0 0 15px rgba(249, 115, 22, 0.4);
}

.plan-type-selector {
  display: flex;
  gap: 20px;
  margin-bottom: 15px;
}

.plan-type-selector label {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #cbd5e1;
  cursor: pointer;
}

.plan-type-selector input[type="radio"] {
  width: auto;
  margin: 0;
}

.template-selector {
  margin-top: 15px;
}

.template-selector label {
  display: block;
  color: #94a3b8;
  margin-bottom: 8px;
}

.template-selector select {
  width: 100%;
  background: rgba(15, 23, 42, 0.8);
  border: 2px solid rgba(148, 163, 184, 0.3);
  color: #e2e8f0;
  padding: 10px 12px;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.template-selector select:focus,
.template-selector select:hover {
  outline: none;
  border-color: #f97316;
  box-shadow: 
    0 0 0 3px rgba(99, 102, 241, 0.1),
    0 0 15px rgba(249, 115, 22, 0.4);
}

.custom-plan {
  margin-top: 15px;
}

.custom-plan label {
  display: block;
  color: #94a3b8;
  margin-bottom: 8px;
}

.custom-plan textarea {
  width: 100%;
  background: rgba(15, 23, 42, 0.8);
  border: 1px solid rgba(148, 163, 184, 0.3);
  color: #e2e8f0;
  padding: 12px;
  border-radius: 8px;
  font-family: 'Fira Code', monospace;
  font-size: 14px;
  line-height: 1.5;
  resize: vertical;
  box-sizing: border-box;
}

.custom-plan textarea:focus {
  outline: none;
  border-color: #6366f1;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

/* Execution Panel */
.execution-panel {
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 16px;
  padding: 30px;
  backdrop-filter: blur(10px);
}

.execution-panel h2 {
  color: #f1f5f9;
  margin: 0 0 25px 0;
  font-size: 1.5rem;
}

.session-info {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.status-item {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.status-label {
  color: #94a3b8;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-value {
  color: #e2e8f0;
  font-weight: 600;
}

.status-value.session-id {
  font-family: 'Fira Code', monospace;
  font-size: 12px;
  color: #a5b4fc;
}

.status-value.initializing {
  color: #fbbf24;
}

.status-value.executing {
  color: #06b6d4;
}

.status-value.completed {
  color: #10b981;
}

.status-value.failed {
  color: #ef4444;
}

/* Results Panel */
.results-panel {
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 16px;
  padding: 30px;
  backdrop-filter: blur(10px);
}

.results-panel h2 {
  color: #f1f5f9;
  margin: 0 0 25px 0;
  font-size: 1.5rem;
}

.results-summary {
  margin-bottom: 30px;
}

.summary-card {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  padding: 20px;
}

.summary-card h3 {
  color: #cbd5e1;
  margin: 0 0 15px 0;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
}

.stat {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.stat-label {
  color: #94a3b8;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
}

.stat-value {
  font-size: 1.5rem;
  font-weight: 700;
  color: #e2e8f0;
}

.stat-value.success {
  color: #10b981;
}

.stat-value.error {
  color: #ef4444;
}

.agents-comparison h3 {
  color: #cbd5e1;
  margin: 0 0 20px 0;
}

.agents-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.agent-card {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
}

.agent-card.success {
  border-color: rgba(16, 185, 129, 0.4);
  box-shadow: 0 0 20px rgba(16, 185, 129, 0.1);
}

.agent-card.failed {
  border-color: rgba(239, 68, 68, 0.4);
  box-shadow: 0 0 20px rgba(239, 68, 68, 0.1);
}

.agent-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(148, 163, 184, 0.2);
}

.agent-header h4 {
  color: #f1f5f9;
  margin: 0;
  font-size: 1.1rem;
}

.agent-status {
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
}

.agent-status.success {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
}

.agent-status.failed {
  background: rgba(239, 68, 68, 0.2);
  color: #ef4444;
}

.agent-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.agent-detail {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10px;
}

.detail-label {
  color: #94a3b8;
  font-size: 12px;
  font-weight: 500;
  min-width: 80px;
}

.detail-value {
  color: #e2e8f0;
  text-align: right;
  font-family: 'Fira Code', monospace;
  font-size: 12px;
  word-break: break-all;
}

.agent-summary {
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid rgba(148, 163, 184, 0.2);
}

.agent-summary .detail-value {
  text-align: left;
  font-family: inherit;
  font-size: 13px;
  line-height: 1.4;
}

.recommendations {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 30px;
}

.recommendations h3 {
  color: #cbd5e1;
  margin: 0 0 15px 0;
}

.recommendations ul {
  margin: 0;
  padding-left: 20px;
  color: #94a3b8;
}

.recommendations li {
  margin-bottom: 8px;
  line-height: 1.5;
}

/* Logs */
.logs-container {
  background: rgba(51, 65, 85, 0.3);
  border: 1px solid rgba(148, 163, 184, 0.2);
  border-radius: 12px;
  overflow: hidden;
}

.logs-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background: rgba(71, 85, 105, 0.3);
  border-bottom: 1px solid rgba(148, 163, 184, 0.2);
}

.logs-header h3 {
  margin: 0;
  color: #cbd5e1;
  font-size: 1.1rem;
}

.log-count {
  color: #94a3b8;
  font-size: 12px;
}

.logs-display {
  max-height: 300px;
  overflow-y: auto;
  padding: 0;
  /* Custom scrollbar styling */
  scrollbar-width: thin;
  scrollbar-color: rgba(99, 102, 241, 0.6) rgba(51, 65, 85, 0.3);
}

.logs-display::-webkit-scrollbar {
  width: 8px;
}

.logs-display::-webkit-scrollbar-track {
  background: rgba(51, 65, 85, 0.3);
  border-radius: 4px;
}

.logs-display::-webkit-scrollbar-thumb {
  background: rgba(99, 102, 241, 0.6);
  border-radius: 4px;
}

.logs-display::-webkit-scrollbar-thumb:hover {
  background: rgba(99, 102, 241, 0.8);
}

.log-entry {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 10px 20px;
  border-bottom: 1px solid rgba(148, 163, 184, 0.1);
  font-family: 'Fira Code', monospace;
  font-size: 13px;
}

.log-entry:last-child {
  border-bottom: none;
}

.log-entry.info {
  color: #94a3b8;
}

.log-entry.success {
  color: #10b981;
  background: rgba(16, 185, 129, 0.05);
}

.log-entry.error {
  color: #ef4444;
  background: rgba(239, 68, 68, 0.05);
}

.log-entry.warning {
  color: #f59e0b;
  background: rgba(245, 158, 11, 0.05);
}

.log-timestamp {
  color: #64748b;
  font-size: 11px;
  min-width: 80px;
}

.log-message {
  flex: 1;
}

/* Control Buttons */
.control-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 30px;
}

.start-btn,
.cleanup-btn,
.new-btn {
  background: linear-gradient(135deg, #6366f1, #8b5cf6);
  border: 2px solid transparent;
  color: white;
  padding: 12px 24px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s ease;
  min-width: 160px;
  position: relative;
}

.start-btn:hover,
.cleanup-btn:hover,
.new-btn:hover {
  transform: translateY(-2px);
  border-color: #f97316;
  box-shadow: 
    0 10px 25px rgba(99, 102, 241, 0.4),
    0 0 20px rgba(249, 115, 22, 0.6),
    0 0 40px rgba(249, 115, 22, 0.3);
}

.start-btn:disabled {
  background: rgba(148, 163, 184, 0.3);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.cleanup-btn {
  background: linear-gradient(135deg, #ef4444, #dc2626);
}

.cleanup-btn:hover {
  box-shadow: 0 10px 25px rgba(239, 68, 68, 0.4);
}

.new-btn {
  background: linear-gradient(135deg, #10b981, #059669);
}

.new-btn:hover {
  box-shadow: 0 10px 25px rgba(16, 185, 129, 0.4);
}

/* Info Footer */
.info-footer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 20px;
  margin-top: 40px;
}

.info-card {
  background: rgba(30, 41, 59, 0.4);
  border: 1px solid rgba(99, 102, 241, 0.3);
  border-radius: 12px;
  padding: 20px;
  backdrop-filter: blur(10px);
}

.info-card h3 {
  color: #cbd5e1;
  margin: 0 0 15px 0;
  font-size: 1.1rem;
}

.info-card ul {
  margin: 0;
  padding-left: 20px;
  color: #94a3b8;
}

.info-card li {
  margin-bottom: 8px;
  line-height: 1.5;
}

/* Responsive Design */
@media (max-width: 768px) {
  .parallel-agent-page {
    padding: 15px;
  }
  
  .page-header h1 {
    font-size: 2rem;
  }
  
  .back-to-ide-btn {
    position: static;
    transform: none;
    margin-bottom: 15px;
  }
  
  .agents-grid {
    grid-template-columns: 1fr;
  }
  
  .control-buttons {
    flex-direction: column;
    align-items: center;
  }
  
  .info-footer {
    grid-template-columns: 1fr;
  }
}