.menu-dropdown {
  position: absolute;
  top: 100%;
  z-index: 1000;
  min-width: 200px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  animation: menuFadeIn 0.15s ease-out;
}

.menu-dropdown.left {
  left: 0;
}

.menu-dropdown.right {
  right: 0;
}

@keyframes menuFadeIn {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.menu-dropdown-content {
  padding: 8px 0;
}

.menu-item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 16px;
  background: transparent;
  border: none;
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.15s ease;
  text-align: left;
  position: relative;
}

.menu-item:hover:not(.disabled) {
  background: var(--bg-tertiary);
}

.menu-item:focus:not(.disabled) {
  background: var(--bg-tertiary);
  outline: none;
}

.menu-item.disabled {
  color: var(--text-muted);
  cursor: not-allowed;
  opacity: 0.5;
}

.menu-item-content {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
}

.menu-item-icon {
  font-size: 16px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-item-label {
  font-weight: 400;
  white-space: nowrap;
}

.menu-item-shortcut {
  font-size: 12px;
  color: var(--text-muted);
  background: var(--bg-primary);
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
}

.menu-item-arrow {
  font-size: 12px;
  color: var(--text-muted);
  margin-left: auto;
}

.menu-separator {
  height: 1px;
  background: var(--border-color);
  margin: 4px 0;
}

.submenu-item {
  position: relative;
}

.submenu-item:hover .submenu-dropdown {
  display: block;
}

.submenu-dropdown {
  display: none;
  position: absolute;
  left: 100%;
  top: 0;
  min-width: 180px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  z-index: 1001;
}

/* Responsive design */
@media (max-width: 768px) {
  .menu-dropdown {
    min-width: 180px;
    font-size: 13px;
  }
  
  .menu-item {
    padding: 10px 14px;
  }
  
  .menu-item-shortcut {
    display: none;
  }
}