.section-selector-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.section-selector-modal {
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  animation: modalSlideIn 0.3s ease;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-secondary);
  border-radius: 12px 12px 0 0;
}

.modal-header h3 {
  margin: 0;
  color: var(--text-primary);
  font-size: 18px;
  font-weight: 600;
}

.close-btn {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  font-size: 18px;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s ease;
}

.close-btn:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.modal-content {
  padding: 24px;
}

.section-info {
  margin-bottom: 24px;
  padding: 16px;
  background: var(--bg-secondary);
  border-radius: 8px;
  border-left: 4px solid var(--accent-purple);
}

.section-type-badge {
  display: inline-block;
  background: var(--accent-purple);
  color: white;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 500;
  text-transform: uppercase;
  margin-bottom: 8px;
}

.section-info p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 14px;
  line-height: 1.5;
}

.suggestions-section, .custom-section {
  margin-bottom: 24px;
}

.suggestions-section h4, .custom-section h4 {
  margin: 0 0 12px 0;
  color: var(--text-primary);
  font-size: 16px;
  font-weight: 600;
}

.suggestions-grid {
  display: grid;
  gap: 8px;
}

.suggestion-btn {
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 12px;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
}

.suggestion-btn:hover {
  background: var(--bg-secondary);
  border-color: var(--accent-purple);
  transform: translateY(-1px);
}

.suggestion-btn.selected {
  background: var(--accent-purple);
  border-color: var(--accent-purple);
  color: white;
}

.suggestion-btn.selected::after {
  content: "✓";
  position: absolute;
  top: 8px;
  right: 8px;
  background: white;
  color: var(--accent-purple);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
}

.suggestion-btn code {
  background: transparent;
  color: inherit;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 13px;
  padding: 0;
  border-radius: 0;
}

.custom-input-wrapper {
  position: relative;
}

.custom-input {
  width: 100%;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 12px;
  color: var(--text-primary);
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 13px;
  outline: none;
  transition: all 0.2s ease;
}

.custom-input:focus {
  border-color: var(--accent-purple);
  box-shadow: 0 0 0 3px rgba(187, 154, 247, 0.1);
}

.custom-input::placeholder {
  color: var(--text-muted);
  font-style: italic;
}

.command-preview {
  margin-top: 8px;
  padding: 8px 12px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  font-size: 12px;
}

.command-preview span {
  color: var(--text-secondary);
}

.command-preview code {
  background: transparent;
  color: var(--accent-purple);
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  padding: 0;
}

.command-examples {
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 16px;
  margin-bottom: 8px;
}

.command-examples h5 {
  margin: 0 0 12px 0;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 600;
}

.command-examples ul {
  margin: 0;
  padding-left: 16px;
  list-style: none;
}

.command-examples li {
  margin: 6px 0;
  position: relative;
}

.command-examples li::before {
  content: "💡";
  position: absolute;
  left: -16px;
  font-size: 12px;
}

.command-examples code {
  background: var(--bg-tertiary);
  color: var(--accent-purple);
  padding: 2px 6px;
  border-radius: 3px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 12px;
}

.modal-footer {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding: 20px 24px;
  border-top: 1px solid var(--border-color);
  background: var(--bg-secondary);
  border-radius: 0 0 12px 12px;
}

.cancel-btn, .replace-btn {
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.cancel-btn {
  background: transparent;
  border: 1px solid var(--border-color);
  color: var(--text-secondary);
}

.cancel-btn:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.replace-btn {
  background: var(--accent-purple);
  border: 1px solid var(--accent-purple);
  color: white;
}

.replace-btn:hover:not(:disabled) {
  background: var(--accent-blue);
  border-color: var(--accent-blue);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(187, 154, 247, 0.3);
}

.replace-btn:disabled {
  background: var(--bg-tertiary);
  border-color: var(--border-color);
  color: var(--text-muted);
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Responsive */
@media (max-width: 768px) {
  .section-selector-modal {
    width: 95%;
    max-height: 90vh;
  }
  
  .modal-header, .modal-content, .modal-footer {
    padding: 16px;
  }
  
  .suggestions-grid {
    grid-template-columns: 1fr;
  }
  
  .modal-footer {
    flex-direction: column;
  }
  
  .cancel-btn, .replace-btn {
    width: 100%;
  }
}