.status-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 24px;
  background: var(--bg-secondary);
  border-top: 1px solid var(--border-color);
  padding: 0 8px;
  font-size: 12px;
  color: var(--text-secondary);
  flex-shrink: 0;
  user-select: none;
  position: relative;
  z-index: 10;
}

.status-left,
.status-center,
.status-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.status-center {
  flex: 1;
  justify-content: center;
}

.status-item {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 2px 6px;
  border-radius: 3px;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.status-item:hover {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.status-icon {
  font-size: 11px;
  flex-shrink: 0;
}

.status-text {
  font-size: 11px;
  font-weight: 400;
}

.generated-indicator {
  color: var(--accent-purple);
  font-size: 10px;
  margin-left: 2px;
}

.generated-count {
  color: var(--accent-purple);
  font-size: 10px;
  font-weight: 500;
  margin-left: 4px;
}

.last-command {
  max-width: 300px;
  overflow: hidden;
}

.last-command .status-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.command-time {
  color: var(--text-muted);
  font-size: 10px;
  margin-left: 4px;
  opacity: 0.7;
}

.project-stats {
  border-left: 1px solid var(--border-color);
  padding-left: 8px;
  margin-left: 4px;
}

.file-stats {
  color: var(--text-muted);
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
}

.react-bits-toggle {
  cursor: pointer;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 2px 8px;
  background: var(--bg-primary);
  transition: all 0.2s ease;
}

.react-bits-toggle:hover {
  border-color: var(--accent-purple);
  background: var(--bg-tertiary);
}

.react-bits-toggle.enabled {
  background: var(--accent-purple);
  color: white;
  border-color: var(--accent-purple);
}

.react-bits-toggle.enabled:hover {
  background: var(--accent-blue);
  border-color: var(--accent-blue);
}

.react-bits-toggle.disabled {
  background: var(--bg-tertiary);
  border-color: var(--border-color);
}

.toggle-indicator {
  font-size: 10px;
  margin-left: 4px;
  font-weight: bold;
}

.toggle-indicator.on {
  color: var(--accent-green);
}

.toggle-indicator.off {
  color: var(--text-muted);
}

.react-bits-toggle.enabled .toggle-indicator {
  color: white;
}

.connection-status {
  color: var(--accent-green);
  font-weight: 500;
}

/* Sleep Mode Status */
.sleep-mode-status {
  background: rgba(74, 222, 128, 0.1);
  border: 1px solid rgba(74, 222, 128, 0.3);
  color: #4ade80;
}

.sleep-indicator.active {
  color: #4ade80;
  font-size: 10px;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

/* Responsive */
@media (max-width: 1200px) {
  .file-stats {
    display: none;
  }
}

@media (max-width: 900px) {
  .last-command {
    max-width: 200px;
  }
  
  .command-time {
    display: none;
  }
}

@media (max-width: 768px) {
  .status-bar {
    padding: 0 4px;
    gap: 8px;
  }
  
  .status-left,
  .status-center,
  .status-right {
    gap: 8px;
  }
  
  .project-stats {
    display: none;
  }
  
  .last-command {
    display: none;
  }
  
  .status-center {
    display: none;
  }
}

@media (max-width: 480px) {
  .status-item .status-text {
    display: none;
  }
  
  .status-item {
    padding: 2px 4px;
  }
  
  .react-bits-toggle .status-text {
    display: inline;
  }
}