.file-explorer {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: var(--bg-secondary);
  border-right: 1px solid var(--border-color);
}

.explorer-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-tertiary);
}

.explorer-header h3 {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.explorer-actions {
  display: flex;
  gap: 4px;
}

.explorer-btn {
  background: transparent;
  border: none;
  color: var(--text-secondary);
  padding: 4px 6px;
  border-radius: 3px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.explorer-btn:hover {
  background: var(--bg-secondary);
  color: var(--text-primary);
}

.explorer-content {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;
}

.project-section, .controls-section, .react-bits-section {
  margin-bottom: 16px;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  color: var(--text-secondary);
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.section-icon {
  font-size: 14px;
}

.file-tree {
  padding: 0;
}

.file-tree-item {
  margin: 0;
}

.file-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 16px;
  cursor: pointer;
  font-size: 14px;
  color: var(--text-primary);
  transition: all 0.2s ease;
  border-radius: 0;
  user-select: none;
}

.file-item:hover {
  background: var(--bg-tertiary);
}

.file-item.active {
  background: var(--accent-blue);
  color: white;
}

.file-item.generated {
  background: rgba(var(--accent-purple-rgb), 0.1);
  border-left: 3px solid var(--accent-purple);
}

.file-item.generated.active {
  background: var(--accent-purple);
}

.file-icon {
  font-size: 16px;
  flex-shrink: 0;
}

.file-name {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.generated-badge {
  font-size: 12px;
  color: var(--accent-purple);
}

.folder-contents {
  /* Handled by padding-left in file-item */
}

.react-bits-section {
  padding: 0 16px;
  margin-top: auto;
  border-top: 1px solid var(--border-color);
  padding-top: 16px;
}

.react-bits-info {
  background: var(--bg-tertiary);
  padding: 12px;
  border-radius: 6px;
  font-size: 12px;
  color: var(--text-secondary);
  line-height: 1.4;
}

.react-bits-info p {
  margin: 0 0 8px 0;
}

.react-bits-commands {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.react-bits-btn {
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  padding: 6px 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.react-bits-btn:hover {
  background: var(--bg-secondary);
  border-color: var(--accent-purple);
}

.react-bits-btn code {
  color: var(--accent-purple);
  font-size: 11px;
  font-family: 'Monaco', 'Menlo', monospace;
  background: transparent;
  padding: 0;
}

/* Scrollbar styling for explorer */
.explorer-content::-webkit-scrollbar {
  width: 6px;
}

.explorer-content::-webkit-scrollbar-track {
  background: transparent;
}

.explorer-content::-webkit-scrollbar-thumb {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.explorer-content::-webkit-scrollbar-thumb:hover {
  background: var(--accent-blue);
}

/* Controls Section */
.controls-section {
  padding: 0 16px;
  border-top: 1px solid var(--border-color);
  padding-top: 16px;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 16px;
}

.controls-buttons {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: flex-start;
}

.control-sidebar-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: var(--text-secondary, #888);
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  width: 50%;
  text-align: left;
}

.control-sidebar-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-primary, #fff);
  transform: translateY(-1px);
}

.control-sidebar-btn.active {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.4);
  color: #bb9af7;
}

.control-sidebar-btn.supervision-active {
  background: rgba(74, 222, 128, 0.2) !important;
  border-color: rgba(74, 222, 128, 0.4) !important;
  color: #4ade80 !important;
}

.control-sidebar-btn.supervision-active:hover {
  background: rgba(74, 222, 128, 0.3) !important;
  color: #22c55e !important;
}

.control-sidebar-btn .control-icon {
  font-size: 14px;
  flex-shrink: 0;
}

.control-sidebar-btn .control-label {
  font-size: 11px;
  font-weight: 500;
  flex: 1;
}