/* Header Layout */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 28px 8px 28px;
  height: 60px;
  background: var(--bg-secondary, #1a1a1a);
  border-bottom: 1px solid var(--border-color, #333);
  color: var(--text-primary, #ffffff);
  position: relative;
}

/* Black padding bars */
.header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 8px;
  background: #000000;
  z-index: 2;
}

.header::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 12px;
  background: #000000;
  z-index: 1;
}

/* Right side black padding */
.header {
  box-shadow: inset -12px 0 0 0 #000000;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 24px;
}

.header-center {
  display: flex;
  align-items: center;
  flex: 1;
  justify-content: center;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Logo Styling */
.logo {
  display: flex;
  align-items: center;
  height: 40px;
}

.logo-image {
  height: 48px;
  width: auto;
  object-fit: contain;
}

.logo-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: opacity 0.2s ease;
  cursor: pointer;
}

.logo-link:hover {
  opacity: 0.8;
}

.logo-link:hover .logo-image {
  transform: scale(1.05);
  transition: transform 0.2s ease;
}

.logo-btn {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

/* Header Actions Styling */
.header-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.action-btn {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  color: var(--text-secondary);
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.action-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  color: var(--text-primary);
  transform: translateY(-1px);
}

.action-btn:active {
  transform: translateY(0);
}

/* SuperClaude button special styling */
.action-btn.superclaude-btn {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(88, 28, 135, 0.1));
  border-color: rgba(139, 92, 246, 0.3);
  animation: pulse 2s ease-in-out infinite;
}

.action-btn.superclaude-btn:hover {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.2), rgba(88, 28, 135, 0.2));
  border-color: rgba(139, 92, 246, 0.5);
  box-shadow: 0 0 20px rgba(139, 92, 246, 0.3);
}

/* Navigation Styles */
.header-nav {
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav-item-container {
  position: relative;
}

.nav-item {
  padding: 8px 12px;
  background: transparent;
  border: none;
  color: var(--text-secondary, #888);
  font-size: 14px;
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.nav-item:hover,
.nav-item.active {
  color: var(--text-primary, #fff);
  background: var(--bg-tertiary, #333);
}

/* View Controls */
.view-controls {
  display: flex;
  gap: 4px;
  align-items: center;
}

.control-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 8px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: var(--text-secondary, #888);
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 70px;
  justify-content: center;
}

.control-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-primary, #fff);
  transform: translateY(-1px);
}

.control-btn.active {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.4);
  color: #bb9af7;
}

.control-btn.supervision-active {
  background: rgba(74, 222, 128, 0.2) !important;
  border-color: rgba(74, 222, 128, 0.4) !important;
  color: #4ade80 !important;
}

.control-btn.supervision-active:hover {
  background: rgba(74, 222, 128, 0.3) !important;
  color: #22c55e !important;
}

.control-icon {
  font-size: 14px;
}

.control-label {
  font-size: 11px;
  font-weight: 500;
}

/* Connection Status */
.connection-status {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  background: rgba(0, 255, 0, 0.1);
  border-radius: 4px;
  font-size: 12px;
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #00ff00;
}

.status-text {
  color: #00ff00;
  font-weight: 500;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(139, 92, 246, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(139, 92, 246, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(139, 92, 246, 0);
  }
}